.containerPrincipal{
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 4% 10%;
  gap: 8%;
  
}

.containerPrincipal-section_1{
  border-radius: 10px;
  box-shadow: 2px 3px 34px -7px rgba(109, 109, 109, 0.473);
}

.containerPrincipal-section_2{
  display: flex;
  flex-direction: column;
  place-items: center;
  margin-top: 10%;
}